.detailsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
}

.detailsImg {
  width: 100%;
  max-width: 600px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.detailsTitle {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.detailsVote {
  font-size: 16px;
  margin-bottom: 10px;
}

.detailsSpan {
  font-weight: bold;
}

.detailsOverview,
.detailsGenres {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 5px;
}

.detailsOverViewText,
.detailsGenresText {
  font-size: 16px;
  margin-bottom: 20px;
  max-width: 300px;
}



p,
h3 {
  margin-block-start: 0;
  margin-inline-end: 0;
}

