.addContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
  background-color: #f2f2f2;
  border-radius: 10px;
  margin-top: 20px;
}

.addText {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

.addLink {
  font-size: 16px;
  margin-bottom: 10px;
  color: #007bff;
  text-decoration: none;
}

.addLink:hover {
  text-decoration: underline;
}


.goBackLink {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.goBackLink:hover {
  background-color: #0056b3;
}